import React, { useRef } from "react"
import { useStaticQuery, graphql } from "gatsby"
import BackgroundImage from "gatsby-background-image"
import { StyledBackgroundImage } from "./style"

type props = {
    name?: string
    className: string
}

const BackgroundPicture: React.FC<props> = ({ name, className }) => {
    const bgRef = useRef()

    const data = useStaticQuery(graphql`
        query {
            allImageSharp {
                nodes {
                    fluid(maxWidth: 800) {
                        ...GatsbyImageSharpFluid
                        originalName
                    }
                }
            }
        }
    `)

    const image = data.allImageSharp.nodes.find(
        (node) => node.fluid.originalName === name
    )

    return (
        <BackgroundImage
            Tag="div"
            ref={bgRef}
            fluid={image.fluid}
            className={className}
            onStartLoad={() => {
                bgRef.current.selfRef.classList.add("loading")
                bgRef.current.selfRef.classList.remove("loading")
            }}
            onLoad={() => {
                bgRef.current.selfRef.classList.remove("loading")
                bgRef.current.selfRef.classList.add("loaded")
            }}
        />
    )
}

export default StyledBackgroundImage(BackgroundPicture)

export const StyledBackgroundImageCentered = StyledBackgroundImage(
    BackgroundPicture,
    true
)
