import React from "react"
import {
    StyledImageSection,
    StyledImageSectionContent,
    StyledImageSectionImage,
} from "./style"
import BackgroundPicture from "../background-image"

type ImageSectionProps = {
    children: React.ReactNode
    imageName: string
    contentWidthPercantage: number
    minHeightPercentage?: number
}

export const ImageSection: React.FC<ImageSectionProps> = ({
    children,
    imageName,
    contentWidthPercantage,
    minHeightPercentage,
}) => {
    return (
        <StyledImageSection
            contentWidthPercantage={contentWidthPercantage}
            minHeightPercentage={minHeightPercentage}
        >
            <StyledImageSectionContent>{children}</StyledImageSectionContent>
            <StyledImageSectionImage>
                <BackgroundPicture name={imageName} />
            </StyledImageSectionImage>
        </StyledImageSection>
    )
}

export const ImageSectionReversed: React.FC<ImageSectionProps> = ({
    children,
    imageName,
    contentWidthPercantage,
    minHeightPercentage,
}) => {
    return (
        <StyledImageSection
            contentWidthPercantage={contentWidthPercantage}
            minHeightPercentage={minHeightPercentage}
            reversed={true}
        >
            <StyledImageSectionImage reversed={true}>
                <BackgroundPicture name={imageName} />
            </StyledImageSectionImage>
            <StyledImageSectionContent reversed={true}>
                {children}
            </StyledImageSectionContent>
        </StyledImageSection>
    )
}
