import styled from "styled-components"

type TechnologyProps = {
    visible: boolean
}

export const StyledTechnology = styled.div<TechnologyProps>`
    position: relative;
    margin: ${props => props.theme.space.small};
    display: flex;
    visibility: ${props => props.visible ? "visible" : "hidden"};
    justify-content: center;
    align-items: center;


    ${({ visible }) => visible && `
        animation: fly-up 0.5s ease-in-out;
    `}

    @keyframes fly-up {
        0%   {
            transform: translateY(500%) scale(0.9);
        }
        100% {
            transform: translateY(0%) scale(1);
        }
    }
`;