import React from "react"

import PageWrapper from "../components/wrappers/index"
import SEO from "../components/seo"
import { ImageSection, ImageSectionReversed } from "../components/sections"
import { Projects } from "../views/projects"
import { Technologies } from "../views/technologies"
import { Footer } from "../components/footer"

const IndexPage = () => (
    <PageWrapper>
        <SEO title="Home" />
        <ImageSection imageName="programmer_1.jpg" contentWidthPercantage={50}>
            <h2>Magdalena Polak</h2>
            <p>Minimalism is the key.</p>
        </ImageSection>
        <ImageSectionReversed
            imageName="me.jpg"
            contentWidthPercantage={60}
            minHeightPercentage={70}
        >
            <h3>About me</h3>
            <p>
                I am a Fullstack Developer with over 2 years of commercial
                experience. I love programming and I focus on improving my
                skills everyday. Code quality and good practices is what I
                really care about.
            </p>
        </ImageSectionReversed>
        <Projects />
        <Technologies />
        <Footer />
    </PageWrapper>
)

export default IndexPage
