import React from "react"
import BackgroundPicture from "../background-image"
import { StyledImageCard, StyledImageCardWithOverlay } from "./style"

type ImageCardProps = {
    imageName: string
    children?: React.ReactNode
}

export const ImageCard: React.FC<ImageCardProps> = ({
    imageName,
    children,
}) => {
    return (
        <StyledImageCard>
            <BackgroundPicture name={imageName} />
            {children}
        </StyledImageCard>
    )
}

export const ImageCardWithOverlay: React.FC<ImageCardProps> = ({
    imageName,
    children,
}) => {
    return (
        <StyledImageCardWithOverlay>
            <BackgroundPicture name={imageName} />
            {children}
        </StyledImageCardWithOverlay>
    )
}
